.tests-holder {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    // height: 100vh;
    padding-top: 60px;
    .test-wrapper {
        width: 900px;
        height: 600px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        border: 1px solid black;
        position: relative;
        @media (max-width: 1366px) {
            height: 550px;
        }

        .current-question {
            width: 100%;
            text-align: center;
            position: absolute;
            bottom: 20px;
            margin: 10px 0;
        }
    }
}
