.test-to-show {
  display: flex;
  width: 300px;
  height: 300px;
  text-align: center;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;


  &.nineth-test{
    align-items: center;
  .symbol-holder{
    align-items: center;
    margin-bottom: 35px;
    position: relative;
    font-size: 30px;
    .character{
      position: absolute;
      display: inline-block;
      width: 20px;
      background: #fff;
      opacity: 1;
      font-size: 30px;;
      &.hidden{
        opacity: 0;
      }
    }
  }
}
}

.buttons{
  width: 100%;
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 100px;

  .button-No{
    margin-left: 50px;
  }
}
